import React from 'react';

const UsersList=(props)=>{
	return(
		
		props.users.map((user,i)=>{
			return(
						<div key={user.id} className='f4-l f5-m f6-ns f7 tc ba v-mid pt4 userlist h3 grow pointer w-100 fr mr0 flex flex-column bb' onClick={()=>{props.userSelect(user)}}>
						{user.name}({user.year}, {user.branch})
						</div>
			)})
	
		)}
export default UsersList;


